import { useCallback, useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useLmsModeratorAllRequestList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import LmsRecommendationAuthorityRequestCard from "./LmsRecommendationAuthorityRequestCard";

const LmsRecommendationAuthorityRequest = ({ modId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    lmsModeratorAllRequestList,
    lmsModeratorAllRequestListLoading,
    lmsModeratorAllRequestListRefetch,
  } = useLmsModeratorAllRequestList({
    data: {
      mid: modId,
      page: page,
      limit: 10,
      search: search,
      flow: "Recommend_Section",
    },
    skip: !modId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsModeratorAllRequestList?.list?.length) {
      let obj = nextPage(lmsModeratorAllRequestList?.list?.length);
      setState(() => obj);
    }
  }, [lmsModeratorAllRequestList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    lmsModeratorAllRequestListRefetch();
  }, [lmsModeratorAllRequestListRefetch]);

  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("leave_request")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        {lmsModeratorAllRequestList?.list?.map((leave, index) =>
          lmsModeratorAllRequestList?.list?.length === index + 1 ? (
            <div key={leave?._id} ref={ref}>
              <LmsRecommendationAuthorityRequestCard
                leave={leave}
                onRefetch={onRefetch}
                modId={modId}
                flow="Recommend_Section"
              />
            </div>
          ) : (
            <LmsRecommendationAuthorityRequestCard
              leave={leave}
              key={leave?._id}
              onRefetch={onRefetch}
              modId={modId}
              flow="Recommend_Section"
            />
          )
        )}
        {lmsModeratorAllRequestListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          lmsModeratorAllRequestList?.list?.length === 0 && (
            <EmptyMessage title="No staff leave request found related this search." />
          )
        ) : (
          lmsModeratorAllRequestList?.list?.length === 0 && (
            <EmptyMessage title="No staff leave request." />
          )
        )}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default LmsRecommendationAuthorityRequest;
